.footer{
    padding: 50px 0px;
    background:#c2a0ee ;
}
.footer-wrappper{
    display: flex;
    /* align-items: center; */
    justify-content: space-between;
    column-gap: 2rem;

}
.footer_box{
    width: 30%;
}
.footer_box p{
    color: var(--heading-color);
    line-height: 28px;
    margin-top: 20px;
}
.footer_title{
    font-size: 1.2rem;
    color: var(--heading-color);
    margin-bottom: 20px;
}
.footer_links li{
    line-height: 28px;
}
.footer_linksli a{
    color: var(--heading-color);
    cursor: pointer;
}
.footer_links li a:hover{
    color: var(--primary-color);
}
.copyrite{
    margin-top: 30px;
    text-align: center;
    color: #777;
    font-size: 0.9rem;
}