.pricing_top{
    text-align: center;
}
.pricing_top h2{
    font-size: 2.5rem;
    margin-bottom: 20px;
}
.pricing_top p{
    font-size: 1.1rem;
    color: var(--headind-color);
    line-height: 28px;
    margin-bottom: 50px;
    
}
 .pricing_wrapper{
    display: flex;
    align-items: center;
    /* justify-content: space-between; */
    column-gap: 2rem;
}

.pricing_item{
    box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px ;
    border-radius: 5px;
    /* width: 33%; */
    background:#fff ;
}
.pricing_card-top{
    padding: 30px;
    border-bottom: 1px solid #cab0e4;
    text-align: center;
 }
 .pricing_card-top h2{
  font-size: 1.5rem; 
  margin-bottom: 0.9rem; 
} 
.pricing_card-top .pricing{
    font-size: 1.4rem;
}
.pricing_card-top .pricng span{
    font-size: 1rem;
    color: #665;
}
.pricing_item .services{
    padding: 30px;

}
.pricing_item .services ul li{
    display: flex;
    align-items: center;
    column-gap: 1.5rem;
    color: #665;
    line-height: 28px;
}
.pricing_item .services ul li span{
    font-size: 0.7rem;
}
.services button{
    margin-top: 50px;

}
.pricing_item-02 .pricing_card-top{
    background: var(--primary-color);
    border-radius: 5px 0px 5px 0px;
}
.pricing_item-02 .pricing_card-top h2{
    color: #fff;
} 
.pricing_item-02 .pricing_card-top h2 span{
    color: rgba(255, 255, 255, 0.795);
}
.pricing_item-02 .services ul li span i{
   color: var(--primary-color);
}   
